import React, { Component } from 'react';

import '../css/main.scss';

import { Link } from 'react-router-dom';
import { PrjListCmp } from '../component';

import AOS from 'aos';
import 'aos/dist/aos.css';

// image import
import img1 from '../images/work/ph_g01.jpg';
import img2 from '../images/work/ph_g02.jpg';
import img3 from '../images/work/ph_g03.jpg';
import img4 from '../images/work/ph_g04.jpg';
import img5 from '../images/work/ph_g05.jpg';
import img6 from '../images/work/ph_g06.jpg';

import prjList from '../projects/projectData.json';

import mainVideo from '../video/magic.mp4';
